/**
 * App entrance
 */


'use strict';

import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import injectTapEventPlugin from 'react-tap-event-plugin';
import { Badge, Button, CheckButton, Form, FormControl, Icon, ImageBox, ImageSlider, PullLoader, Tab, Table, TopAction } from '../src/components/index';


injectTapEventPlugin();

const formSelectOptions = [
  {
    value: '',
    text: ''
  },
  {
    value: '1',
    text: ''
  },
  {
    value: '2',
    text: ''
  }
];

const formData = {
  action: '/form',
  controls: [
    {
      name: 'name',
      type: 'text',
      label: '',
      placeholder: '',
      required: true
    },
    {
      name: 'address',
      type: 'text',
      label: '',
      placeholder: ' 100 ',
      required: true,
      maxLength: 100
    }
  ],
  submitText: ''
};

const formData2 = {
  action: '/form',
  controls: [
    {
      name: 'mobile',
      type: 'tel',
      placeholder: '',
      required: true
    },
    {
      name: 'code',
      type: 'tel',
      placeholder: '',
      required: true,
      maxLength: 4,
      minLength: 4
    }
  ],
  submitText: '',
  beforeSubmit(formData) {
    // console.log(formData);
  },
  onSubmit(formData, form) {
    console.log(formData, form);
  }
};

const imageSliderData = [
  'src/images/400_600.jpeg',
  'src/images/400_300.jpeg',
  'src/images/400_400.jpeg'
];

const tabBarData = [
  {
    icon: 'home',
    text: '',
    link: 'home'
  },
  {
    badge: '9',
    icon: 'gift-box',
    text: '',
    link: 'gift-box'
  },
  {
    icon: 'user',
    text: '',
    link: 'user'
  },
  {
    icon: 'shop',
    text: '',
    link: '//m.taobao.com'
  }
];

const tabNavData = [
  {
    icon: 'physical',
    text: '',
    link: 'physical',
    type: 1
  },
  {
    icon: 'virtual',
    text: '',
    link: 'virtual',
    type: 2
  },
  {
    icon: 'discount',
    text: '',
    link: 'discount',
    type: 3
  },
  {
    icon: 'game',
    text: '',
    link: 'game',
    type: 4
  }
];

const tableData = [
  {
    text: '',
    note: '2015-10-10',
    href: 'profile',
    disclosure: true
  },
  {
    text: '',
    href: 'avatar',
    children: <img style={{ width: 56, height: 56 }} src='src/images/100_100.jpeg' alt='' />
  },
  {
    text: '',
    note: '2015-10-10',
    href: 'nickname',
    children: ''
  },
  {
    text: '',
    href: 'address',
    children: <span className='text-lightest'></span>,
    disclosure: true
  }
];

class TestPage extends Component {

  constructor(props) {
    super(props);
    this.pulledToBottom = this.pulledToBottom.bind(this);

    this.state = {
      bottom: false
    };
  }

  render() {
    const { bottom } = this.state;

    return (
      <div>
        <h2 className='gap-side gap-t'>Icon</h2>
        <div className='gap-side'>
          <Icon name='loading'></Icon>
          <Icon name='rotate' className='gap-l'></Icon>
        </div>

        <h2 className='gap-side gap-t'></h2>
        <div className='gap-side'>
          <Button disabled onTouchTap={this.handleEvents}></Button>
          <Button type='button' onTouchTap={this.handleEvents} className='gap-t'></Button>
          <Button type='button' icon='rotate' className='gap-t' disabled></Button>
        </div>

        <h2 className='gap-side gap-t'></h2>
        <div className='gap-side'>
          <Button disabled icon='delete' link></Button>
          <Button icon='edit' link></Button>
        </div>

        <h2 className='gap-side gap-t'></h2>
        <div className='gap-side'>
          <CheckButton onToggle={this.handleEvents}></CheckButton>
          <CheckButton checked></CheckButton>
        </div>

        <h2 className='gap-side gap-t'></h2>
        <div className='gap-side'>
          <FormControl name='text' type='text' placeholder='' defaultValue='' onChange={this.handleEvents} required />
          <FormControl name='number' type='tel' placeholder='5-10' max='10' min='5' />
          <FormControl name='email' type='email' placeholder='' />
          <FormControl name='tel' type='tel' placeholder='' />
          <FormControl name='date' type='date' placeholder='' required />
          <FormControl name='time' type='time' placeholder='' required />
          <FormControl name='select' type='select' placeholder='' options={formSelectOptions} required />
          <FormControl name='textarea' type='textarea' placeholder='510 ' maxLength='10' minLength='5' />
        </div>

        <h2 className='gap-side gap-t'></h2>
        <Form {...formData2} className='gap-t'></Form>

        <h2 className='gap-side gap-t'></h2>
        <Form {...formData}></Form>

        <h2 className='gap-side gap-t'>Tab.Nav</h2>
        <Tab.Nav data={tabNavData}></Tab.Nav>

        <h2 className='gap-side gap-t'>Tab.Bar ()</h2>
        <Tab.Bar data={tabBarData}></Tab.Bar>

        <h2 className='gap-side gap-t'>Table</h2>
        <Table data={tableData} expanded={true}>
          <span></span>
          <span className='gap-l'></span>
          <span className='text-primary'>600</span>
          <span className='gap-l'></span>
          <span className='text-primary'>600</span>
        </Table>

        <h2 className='gap-side gap-t'>ImageBox</h2>
        <div className='gap-side'>
          <div style={{ display: 'inline-block' }}>
            <ImageBox src='src/images/200_150.jpeg'></ImageBox>
          </div>
          <div style={{ display: 'inline-block' }} className='gap-l'>
            <ImageBox src='src/images/150_200.jpeg'></ImageBox>
          </div>
        </div>

        <h2 className='gap-side gap-t'>ImageSlider</h2>
        <ImageSlider data={imageSliderData}></ImageSlider>

        <h2 className='gap-side gap-t'>TopAction ()</h2>
        <TopAction></TopAction>

        <h2 className='gap-side gap-t'>PullLoader ({ bottom ? '' : ' onPull'})</h2>
        <PullLoader onPull={this.pulledToBottom}></PullLoader>
      </div>
    );
  }

  handleEvents(e) {
    console.log(e);
  }

  pulledToBottom() {
    this.setState({
      bottom: true
    });
  }
}

ReactDOM.render((
  <TestPage></TestPage>
), document.getElementById('app'));


